<template>
    <header class="header">
        <div class="header__content" :class="{ 'scrolled-nav': scrolledNav }">
            <div class="header__container">
                <div class="header__wrapper">
                    <nav class="header__navigation">
                        <div class="header__logo">
                            <router-link :to="{ name: 'home' }"><img src="@/assets/logo.png" alt="1" /></router-link>
                        </div>
                        <div class="header__menu-body">
                            <ul v-show="!mobile" class="menu__list">
                                <li class="block-actions__item">
                                    <router-link class="menu__link" :to="{ name: 'home' }">Home</router-link>
                                </li>
                                <li class="block-actions__item">
                                    <router-link class="menu__link" :to="{ name: 'shop' }">Shop</router-link>
                                </li>
                                <li class="block-actions__item">
                                    <router-link class="menu__link" :to="{ name: 'about' }">About</router-link>
                                </li>
                                <li class="block-actions__item">
                                    <router-link class="menu__link" :to="{ name: 'contact' }">Contact</router-link>
                                </li>
                            </ul>
                        </div>
                        <div class="header__menu-action action-menu">
                            <ul v-show="!mobile" class="action-menu__list">
                                <li class="action-menu__item">
                                    <router-link class="action-menu__link" :to="{ name: 'My Account' }"
                                        ><font-awesome-icon :icon="['fas', 'right-to-bracket']"
                                    /></router-link>
                                </li>
                                <li class="action-menu__item">
                                    <router-link class="action-menu__link" :to="{ name: 'favorite-page' }"
                                        ><font-awesome-icon :icon="['far', 'heart']"
                                    /></router-link>
                                </li>
                                <li class="action-menu__item">
                                    <router-link class="action-menu__link" :to="{ name: 'cart' }"
                                        ><font-awesome-icon :icon="['fas', 'cart-shopping']"
                                    /></router-link>
                                </li>
                            </ul>
                        </div>
                        <div class="menu__icon">
                            <font-awesome-icon
                                v-show="mobile"
                                class="far fa-bars"
                                :class="{ 'menu-open': mobileNav }"
                                :icon="['fas', 'bars']"
                                @click="toggleMobileNav"
                            />
                        </div>
                        <transition name="mobile-nav" class="mobile-nav">
                            <ul v-show="mobileNav" class="dropdown-nav">
                                <li class="dropdown-nav__item">
                                    <router-link class="dropdown-nav__link" :to="{ name: 'home' }">Home</router-link>
                                </li>
                                <li class="dropdown-nav__item">
                                    <router-link class="dropdown-nav__link" :to="{ name: 'shop' }">Shop</router-link>
                                </li>
                                <li class="dropdown-nav__item">
                                    <router-link class="dropdown-nav__link" :to="{ name: 'about' }">About</router-link>
                                </li>
                                <li class="dropdown-nav__item">
                                    <router-link class="dropdown-nav__link" :to="{ name: 'contact' }"
                                        >Contact</router-link
                                    >
                                </li>
                            </ul>
                        </transition>
                    </nav>
                </div>
            </div>
        </div>
    </header>
    <transition name="slide" mode="out-in">
        <router-view />
    </transition>
</template>

<script>
export default {
    name: 'HeaderSecond',
    data() {
        return {
            scrolledNav: null,
            mobile: null,
            mobileNav: null,
            windowWidth: null,
        }
    },
    created() {
        window.addEventListener('resize', this.checkScreen)
        this.checkScreen()
    },
    mounted() {
        window.addEventListener('scroll', this.updateScroll)
        this.updateScroll()
    },
    methods: {
        toggleMobileNav() {
            this.mobileNav = !this.mobileNav
        },
        updateScroll() {
            const scrollPosition = window.scrollY
            if (scrollPosition > 50) {
                this.scrolledNav = true
                return
            }
            this.scrolledNav = false
        },
        checkScreen() {
            this.windowWidth = window.innerWidth
            if (this.windowWidth <= 991.91) {
                this.mobile = true
                return
            }
            this.mobile = false
            this.mobileNav = false
            return
        },
    },
}
</script>

<style lang="scss" scoped>
@import '@/assets/index.scss';
.header {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100%;
    transition: background-color 0.3s ease 0s;
    background-color: #fbebb5;

    // .header__content

    &__content {
        position: relative;
    }

    // .header__container
    nav {
    }
    &__container {
    }

    // .header__wrapper

    &__wrapper {
        // padding: 0px 20px;
    }

    // .header__navigation

    &__navigation {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    // .header__logo

    &__logo {
        position: relative;
        z-index: 99;
    }

    // .header__menu-body

    &__menu-body {
    }
    // .mobile-nav-enter-active,
    // .mobile-nav-leave-active {
    //     transition: all 0.3s ease 0s;
    // }
    // .mobile-nav-enter-form,
    // .mobile-nav-leave-to {
    //     transition: translateX(100%);
    // }
    // .mobile-nav-enter-to {
    //     transform: translateX(0);
    // }
}
.menu {
    // .menu__list

    &__list {
        display: flex;
        gap: 44px;
        flex: 1;
    }

    // .menu__link

    &__link {
        margin-top: 20px;
        color: black;
        color: black;
        position: relative;
        color: #000;
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 13px;

        &::after {
            position: absolute;
            content: '';
            left: 0;
            bottom: -4px;
            display: block;
            width: 100%;
            height: 2px;
            background: #000;
        }
        &::after {
            width: 0%;
            transition: 0.3s;
        }
        &:hover {
            &::after {
                width: 100%;
                right: 0;
            }
        }
    }

    // .menu__icon

    &__icon {
        z-index: 5;
        display: flex;
        align-items: center;
        position: absolute;
        top: 0;
        right: 24px;
        height: 100%;
    }
}
.block-actions {
    // .block-actions__item

    &__item {
    }
}
.fa-bars {
    cursor: pointer;
    font-size: 24px;
    transition: all 0.3s ease 0s;
}
.menu-open {
    transform: rotate(180deg);
    transition: all 0.3s ease 0s;
}
.mobile-nav {
    display: flex;
    flex-direction: column;
    text-align: center;
    gap: 25px;
    position: fixed;
    width: 250px;
    max-width: 100%;
    height: 100%;
    background-color: #fff;
    top: 0;
    left: 0;
    font-size: 32px;

    .mobile-nav-enter-active,
    .mobile-nav-leave-active {
        transition: all 0.3s ease 0s;
    }
    .mobile-nav-enter-form,
    .mobile-nav-leave-to {
        transition: translateX(100%);
        transition: all 0.3s ease 0s;
    }
    .mobile-nav-enter-to {
        transform: translateX(0);
        transition: all 0.3s ease 0s;
    }
    body {
        overflow: hidden;
    }
}
.dropdown-nav {
    z-index: 3;
    padding-top: 120px;
    // .dropdown-nav__item
    &__item {
    }

    // .dropdown-nav__link

    &__link {
    }
}
.scrolled-nav {
    background-color: #fbebb5;
    nav {
        // padding: 8px 0;
    }
    .header__logo {
        img {
            width: 60px;
        }
    }
}
.header {
    // .header__menu-action

    &__menu-action {
    }
}
.action-menu {
    // .action-menu__list

    &__list {
        display: flex;
        gap: 45px;
    }

    // .action-menu__item

    &__item {
    }

    // .action-menu__link

    &__link {
        font-size: 28px;
    }
}
.slide-enter-active,
.slide-leave-active {
    transition: opacity 0.5s;
}

.slide-enter-from,
.slide-leave-to {
    opacity: 0;
}
</style>
