import { createApp } from 'vue'
import { createRouter, createWebHistory } from 'vue-router'
import TransitionWrapper from '../components/transition/TransitionWrapper.vue'

const routes = [
    {
        path: '/',
        name: 'home',
        component: () => import(/* webpackChunkName: "about" */ '../views/HomeView.vue'),
    },
    {
        path: '/shop',
        name: 'shop',
        component: () => import(/* webpackChunkName: "about" */ '../views/ShopView.vue'),
    },
    {
        path: '/about',
        name: 'about',
        component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue'),
    },
    {
        path: '/cart',
        name: 'cart',
        component: () => import(/* webpackChunkName: "about" */ '../views/CartView.vue'),
    },
    {
        path: '/contact',
        name: 'contact',
        component: () => import(/* webpackChunkName: "about" */ '../views/ContactView.vue'),
    },
    {
        path: '/item-',
        name: 'item',
        component: () => import(/* webpackChunkName: "about" */ '../views/SingleProductView.vue'),
    },
    {
        path: '/login-page',
        name: 'My Account',
        component: () => import(/* webpackChunkName: "about" */ '../views/MyAccount.vue'),
    },
    {
        path: '/checkout',
        name: 'checkout',
        component: () => import(/* webpackChunkName: "about" */ '../views/CheckoutView.vue'),
    },
    {
        path: '/blog',
        name: 'blog',
        component: () => import(/* webpackChunkName: "about" */ '../views/BlogView.vue'),
    },
    {
        path: '/favorite-page',
        name: 'favorite-page',
        component: () => import(/* webpackChunkName: "about" */ '../views/FavoritePage.vue'),
    },
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
})
router.beforeEach((to, from, next) => {
    window.scrollTo(0, 0) // Прокручиваем страницу вверх
    next() // Продолжаем переход на новый маршрут
})
const app = createApp(TransitionWrapper)
app.use(router)
app.mount('#app')
export default router
